  },
  methods: {
    searchitem(value) {
      value = value.join("/");
      this.article_list = [];
      this.article_list_all.forEach((item) => {
        if (item.article_major.indexOf(value) != -1) {
          this.article_list.push(item);
        }
      });
    },
    handleSelect(key) {
      let loading;
      //定义加载动画
      function startLoading() {
        loading = Loading.service({
          lock: true,
          text: "少女祈祷中...",
          background: "rgba(0,0,0,0.4)",
        });
      }
      function endLoading() {
        loading.close();
      }

      if (key == 1) {
        startLoading();
        this.activeIndex = "1";
        this.keywords = "";
        this.searchCount = 0;
        this.article_list = [];
        this.searchchoicenum = 0;
        this.searchchoice = [];
        if (JSON.parse(localStorage.getItem("keywords_list")).length >= 2) {
          let max1,
            max2 = "";
          let keywords_sort = [];
          JSON.parse(localStorage.getItem("keywords_list")).forEach((item) => {
            keywords_sort.push(item);
          });
          keywords_sort.sort((a, b) => {
            return b.keywords_count - a.keywords_count;
          });
          max1 = keywords_sort[0].keywords_name;
          max2 = keywords_sort[1].keywords_name;

          this.messageList.forEach((item, index) => {
            if (item.toLowerCase().indexOf(max1.toLowerCase()) != -1) {
              this.article_recommond.push(this.article_list_all[index]);
            }
          });
          this.article_recommond = this.shuffle(this.article_recommond);
          this.messageList.forEach((item, index) => {
            if (item.toLowerCase().indexOf(max1.toLowerCase()) == -1) {
              if (item.toLowerCase().indexOf(max2.toLowerCase()) != -1) {
                this.article_recommond.push(this.article_list_all[index]);
              }
            }
          });
          this.article_recommond = this.shuffle(this.article_recommond);
          this.messageList.forEach((item, index) => {
            if (item.toLowerCase().indexOf(max1.toLowerCase()) == -1) {
              if (item.toLowerCase().indexOf(max2.toLowerCase()) == -1) {
                this.article_recommond.push(this.article_list_all[index]);
              }
            }
          });
          this.article_list = this.article_recommond;
        } else if (
          JSON.parse(localStorage.getItem("keywords_list")).length == 1
        ) {
          let max1 = "";
          max1 = JSON.parse(localStorage.getItem("keywords_list"))[0]
            .keywords_name;
          this.messageList.forEach((item, index) => {
            if (item.toLowerCase().indexOf(max1.toLowerCase()) != -1) {
              this.article_recommond.push(this.article_list_all[index]);
            }
          });
          this.article_recommond = this.shuffle(this.article_recommond);
          this.messageList.forEach((item, index) => {
            if (item.toLowerCase().indexOf(max1.toLowerCase()) == -1) {
              this.article_recommond.push(this.article_list_all[index]);
            }
          });
          this.article_list = this.article_recommond;
        } else {
          this.article_list = this.article_list_all;
        }
        endLoading();
      }
      if (key == 2) {
        this.searchCount++;
        this.keywords = "";
        this.article_list = [];
        this.searchchoicenum = 1;
        this.searchchoice = [];
        this.article_list_all.forEach((item) => {
          if (item.article_major.indexOf("文学") != -1) {
            this.article_list.push(item);
          }
        });
      }
      if (key == 3) {
        this.searchCount++;
        this.keywords = "";
        this.article_list = [];
        this.searchchoicenum = 2;
        this.searchchoice = [];
        this.article_list_all.forEach((item) => {
          if (item.article_major.indexOf("理学") != -1) {
