          }
        });
      }
      if (key == 6) {
        this.searchCount++;
        this.keywords = "";
        this.article_list = [];
        this.searchchoicenum = 5;
        this.searchchoice = [];
        this.article_list_all.forEach((item) => {
          if (item.article_major.indexOf("农学") != -1) {
            this.article_list.push(item);
          }
        });
      }
      if (key == 7) {
        this.searchCount++;
        this.keywords = "";
        this.article_list = [];
        this.searchchoicenum = 0;
        this.searchchoice = [];
        this.article_list_all.forEach((item) => {
          if (item.article_major.indexOf("其他学科") != -1) {
            this.article_list.push(item);
          }
        });
      }
    },
    search(keywords) {
      if (!keywords) {
        this.$notify({
          title: "请输入搜索内容",
          type: "error",
        });
      } else {
        this.searchCount++;
        const paramsList = {
          keyword: keywords,
          user_id: JSON.parse(localStorage.getItem("user_info")).id,
        };
        submitSearchKeywordAPI(paramsList);
        let index = 0;
        this.article_list.splice(0);
        this.messageList.forEach((item) => {
          if (item.toLowerCase().indexOf(keywords.toLowerCase()) != -1) {
            this.article_list.push(this.article_list_all[index]);
          }
          index++;
        });
        if (this.article_list.length == 0) {
          // 提示总共搜索到几篇文章
          this.$notify({
            title: "未找到相关文章",
            type: "error",
          });
        } else {
          // 提示总共搜索到几篇文章
          this.$notify({
            title: "搜索成功！",
            message: `总共搜索到${this.article_list.length}篇文章`,
            type: "success",
          });

          let keywords_list = [];
          let flag = 0;
          if (localStorage.getItem("keywords_list")) {
            JSON.parse(localStorage.getItem("keywords_list")).forEach(
              (item) => {
                if (
                  item.keywords_name.toLowerCase() == keywords.toLowerCase()
                ) {
                  flag = 1;
                  item.keywords_count++;
                }
                keywords_list.push(item);
              }
            );
            if (flag == 0) {
              keywords_list.push({
                keywords_name: keywords.toLowerCase(),
                keywords_count: 1,
              });
            }
            localStorage.setItem(
              "keywords_list",
              JSON.stringify(keywords_list)
            );
          } else {
            keywords_list.push({
              keywords_name: keywords.toLowerCase(),
              keywords_count: 1,
            });
            localStorage.setItem(
              "keywords_list",
              JSON.stringify(keywords_list)
            );
          }
        }
      }
    },
    getRandomInt(min, max) {
      return Math.floor(Math.random() * (max - min + 1) + min); // +1是保证可以取到上限值
    },
    shuffle(arr) {
      let _arr = arr.slice();
      for (let i = 0; i < _arr.length; i++) {
        let j = this.getRandomInt(0, i);
        let t = _arr[i];
        _arr[i] = _arr[j];
        _arr[j] = t;
      }
      return _arr;
    },
  },
  components: {
    HomeNewsListVue,
    HomeAuthorListVue,
    HomeFoucsListVue,
