<template>
  <div name="newHomeItem" class="newHomeItem-wrap">
    <el-row type="flex">
      <el-col :span="0.5">
        <el-tooltip
          class="item"
          effect="light"
          content="点击前往个人主页"
          placement="left"
        >
          <div class="author_head" @click="push(2)">
            <img :src="author_head" alt="" />
          </div>
        </el-tooltip>
      </el-col>
      <el-col :span="0.5" style="margin: 0 10px 0 10px">
        <el-row>
          <div
            class="article_title"
            @click="push(1)"
            style="margin: -3px 0 0 0"
          >
            <font>{{ article_title }}</font>
          </div>
        </el-row>
        <el-row>
          <div
            class="article_introduce"
            @click="push(1)"
            style="margin: 3px 0 0 0"
          >
            <font>{{ article_introduce }}</font>
          </div>
        </el-row>
      </el-col>
    </el-row>
    <el-row type="flex">
      <el-col :span="0.5">
        <el-row style="margin-top: 5px" type="flex">
          <font class="author_name">{{ author_name }}</font>
          <div class="author_university" style="margin-left: 10px">
            <font>{{ author_university }}</font>
          </div>
        </el-row>
        <el-row
          style="margin-top: 5px; flex-flow: row wrap"
          type="flex"
          class="article_labels"
        >
          <font>标签：</font>
          <div v-for="(item, index) in article_labels" :key="index">
            <font>{{ article_labels[index] }}</font>
          </div>
          <font v-if="article_labels.length == 0">暂无相关标签</font>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "newHomeItem",
  data() {
    return {
      id: this.newsList.article_id,
      article_status: this.newsList.article_status,
      author_id: this.newsList.author_id,
      author_name: this.newsList.author_name,
      author_head: this.newsList.author_headphoto,
      author_university: this.newsList.author_university,
      article_title: this.newsList.article_title,
      article_labels: this.newsList.article_labels,
      article_introduce: this.newsList.article_introduce,
    };
  },
  methods: {
    push(num) {
      if (num == 1) {
        const routeUrl = this.$router.resolve({
          path: "/articleHome/" + this.id,
        });
        window.open(routeUrl.href, "_blank");
      }
      if (num == 2) {
        this.$router.push({
          path: "/personalCenter/" + this.author_id,
        });
      }
    },
  },
  props: ["newsList"],
};
</script>

<style scoped>
.newHomeItem-wrap {
  border: 0.5px solid #00ead8;
  border-radius: 20px;
  margin: 10px 0;
  padding: 10px;
  position: relative;
  width: 280px;
}
.author_head {
  width: 36px;
  height: 36px;
  background-color: #76fff5;
  border-radius: 18px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.author_head img {
  width: 36px;
}
.article_title font {
  font-family: SourceHanSansCN-Medium;
  font-size: 16px;
  font-weight: 500;
  color: #3d3d3d;
  cursor: pointer;
}
.article_title:hover font {
  color: #00ead8;
}
.article_introduce {
  font-family: SourceHanSansCN-Medium;
  font-size: 10px;
  font-weight: normal;
  color: #9e9e9e;
  /* 以下是显示固定行数的文字的css，多余文字用省略号表示 */
  text-overflow: ellipsis;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  word-wrap: break-word;
  word-break: break-all;
  cursor: pointer;
}
.author_name {
  height: 17px;
  font-family: SourceHanSansCN-Regular;
  font-size: 12px;
  font-weight: normal;
  color: #3d3d3d;
}
.article_labels > div {
  width: max-content;
  margin: 0 5px;
  padding: 0 10px;
  height: 20px;
  border-radius: 10px;
  background: #d9fe32;
  display: flex;
  justify-content: center;
  align-items: center;
}
.article_labels > font {
  font-family: SourceHanSansCN-Regular;
  font-size: 12px;
  font-weight: normal;
  color: #3d3d3d;
}
.author_university {
  width: max-content;
  padding: 0 10px;
  height: 20px;
  border-radius: 10px;
  background: #bb76ff;
  display: flex;
  justify-content: center;
  align-items: center;
}
.author_university font {
  font-family: SourceHanSansCN-Regular;
  font-size: 10px;
  font-weight: normal;
  color: #ffffff;
}
</style>