import myAxios from './axios';

// 获取文章列表
export function getArticleListAPI(paramsList) {
  return myAxios({
    url: '/article/articlelist',
    method: 'get',
    params: paramsList
  })
}

// 获取文章详情内容
export function getArticleMainAPI(paramsList) {
  return myAxios({
    url: '/article/articlemain',
    method: 'get',
    params: paramsList
  })
}

// 上传文章
export function postArticleAPI(paramsList) {
  return myAxios({
    url: '/article/postarticle',
    method: 'post',
    data: paramsList
  })
}

// 新增文章标签
export function addArticleLabelAPI(paramsList) {
  return myAxios({
    url: '/article/addlabels',
    method: 'post',
    data: paramsList
  })
}

// 提交搜索关键词
export function submitSearchKeywordAPI(paramsList) {
  return myAxios({
    url: '/article/submitkeyword',
    method: 'post',
    data: paramsList
  })
}

// 添加文章书签
export function addBookMarkAPI(paramsList) {
  return myAxios({
    url: '/article/postbookmark',
    method: 'post',
    data: paramsList
  })
}

// 获取指定用户的文章书签
export function getBookMarkAPI(paramsList) {
  return myAxios({
    url: '/article/getbookmark',
    method: 'get',
    params: paramsList
  })
}

// 删除指定用户的指定文章书签
export function removeBookMarkAPI(paramsList) {
  return myAxios({
    url: '/article/removebookmark',
    method: 'post',
    data: paramsList
  })
}