<script>
export default {
  name: "commonHeader",
  data() {
    return {
      keyvalue: "",
      user_head: "",
      user_id: "",
    };
  },
  methods: {
    push(num) {
      let route_path = "";
      if (num == 0) {
        route_path = "/home";
      }
      if (num == 1) {
        route_path = "/postArticle";
      }
      if (num == 2) {
        route_path = "/personalCenter/" + this.user_id;
      }
      if (num == 3) {
        route_path = "/login";
      }
      this.$router.push({
        path: route_path,
      });
    },
    loginchoices(num) {
      if (num == 1) {
        this.push(2);
      }
      if (num == 2) {
        window.localStorage.clear();
        this.push(3);
      }
    },
  },
  mounted() {
    if (localStorage.getItem("user_info")) {
      this.user_head = JSON.parse(localStorage.getItem("user_info")).headphoto;
      this.user_id = JSON.parse(localStorage.getItem("user_info")).id;
    }
  },
};
</script>

